@import '../../assets/css/var.scss';
@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.mobilePositionList {
  .empty-wrap {
    padding: 6px 0 10px;
    background: rgb(54, 45, 74);
    border-radius: 5px;
    @include lightThemeOnly {
      background: #ffffff;
    }
  }
  .empty-content {
    min-height: 80px;
  }

  .sun-empty-wrap {
    border-radius: 5px;
    .sun-empty-img {
      width: auto;
    }
  }

  .card-wrap {
    .positionCard {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
