@import './var.scss';
@import './theme';
@import './mixins';

.sunCurve-wrap {
  .sunCurve-header-tvl {
    width: 90%;
    margin: 50px auto 0;
    max-width: 1200px;
    @include mobile-only {
      margin: 30px auto 0;
    }
  }
  .sunCurve-main {
    margin: 0 auto;
    @include common-content-width;
    .list-holder {
      min-height: 200px;
    }
  }
  .sunCurve-main-inner {
    border-radius: 20px;
    background: $color-table-bg;
    overflow: hidden;
  }
  .sunCurve-thead {
    display: flex;
    padding: 0 50px;
    font-size: 14px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    background: $color-table-header-bg;
    color: $color-text-default-5;
    .sunCurve-thead-l,
    .sunCurve-thead-c {
      margin-right: 10px;
    }
    .sunCurve-thead-l,
    .sunCurve-thead-c,
    .sunCurve-thead-r {
      padding: 20px 0;
    }
    .sunCurve-thead-l {
      width: calc(max(50%, 500px));
    }
    .sunCurve-thead-c,
    .sunCurve-thead-r {
      flex: 1;
    }
  }
  .sunCurve-list {
    li {
      position: relative;
      display: flex;
      padding: 20px 40px;
      font-size: 15px;
      background: $color-table-bg;
      line-height: 30px !important;
      border-bottom: 1px solid $color-border-primary;
      cursor: pointer;
      // &:last-child {
      //   border-bottom: 0;
      // }
      .sunCurve-value {
        font-size: 20px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: $color-text-default;
      }
      .sunCurve-label {
        font-size: 14px;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: $color-text-default;
        opacity: 0.7;
      }
    }
    .sunCurve-row-l,
    .sunCurve-row-c {
      margin-right: 10px;
    }
    .sunCurve-row-c,
    .sunCurve-row-r {
      .sunCurve-label {
        display: none;
      }
    }
    .sunCurve-row-l {
      width: calc(max(50%, 500px));
      .sunCurve-value {
        margin-right: 10px;
      }
    }
    .sunCurve-row-c {
      flex: 1;
    }
    .sunCurve-row-r {
      flex: 1;
    }
    .sunCurve-row {
      display: flex;
    }
    .sunCurve-row-l {
      .pool-icon {
        margin-right: 10px;
        height: 30px;
        img {
          display: block;
          height: 100%;
        }
      }
    }
    .icon-right {
      position: absolute;
      right: 30px;
      top: 50%;
      margin-top: -5px;
      width: 10px;
      height: 10px;
    }
  }
  .more-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px 0;
    line-height: 21px;
    font-size: 15px;
    font-family: PingFangSC-Semibold, PingFang SC;
    background: $color-table-bg;
    font-weight: 600;
    color: $color-text-default;
    cursor: pointer;
    border-top: $color-border-primary;
    img {
      display: block;
      margin-left: 10px;
      width: 10px;
      height: 10px;
    }
  }
  .more-btn-active {
    img {
      transform: rotate(180deg);
    }
  }
  .sunCurve-loading {
    .ant-spin-container::after {
      background: transparent;
    }
  }

  .sunCurve-deposite {
    margin: 0 auto;
    max-width: 1200px;
    @include common-content-width;
    .sunCurve-deposite-header {
      display: flex;
      margin: 60px auto 20px;
      color: $color-text-default;
      font-family: PingFangSC, PingFang SC;
      font-size: 20px;
      font-weight: 600;
      color: $color-text-default;
      line-height: 28px;
      .sunCurve-deposite-l {
        display: flex;
        align-items: center;
        line-height: 28px;
        .ant-switch-inner {
          background: #cccccc;
        }
        .ant-switch-checked {
          .ant-switch-inner {
            background: $color-button-primary;
          }
        }
      }
      .sunCurve-deposite-setting {
        margin-left: 10px;
      }
      .sunCurve-deposite-total {
        flex: 1;
        width: 0;
        height: 28px;
        text-align: right;
      }
      .sunCurve-deposite-total-label {
        margin-right: 10px;
        line-height: 30px;
        font-size: 14px;
        font-weight: 400;
        vertical-align: middle;
        color: $color-text-default-5;
      }
      .sunCurve-deposite-total-num {
        line-height: 30px;
        vertical-align: middle;
      }
    }
    .sunCurve-deposite-list {
      border-radius: 0px;
      overflow: hidden;
      min-height: 100px;
      .ant-table {
        border-radius: 20px;
        overflow: hidden;
        .ant-table-thead {
          th {
            border: 0;
          }
        }
        .ant-table-tbody {
          tr {
            &:last-child {
              td {
                border-bottom: 0;
              }
            }
          }
        }
      }
      .sun-empty-wrap {
        margin-top: 10px;
        margin-bottom: 10px;
        min-height: auto;
        text-align: center;
        background: var(--color-card-bg-active);
      }
      // .ant-table-thead {
      //   th {
      //     color: rgba(255, 255, 255, 0.5);
      //   }
      // }

      .loading-content {
        height: 80px;
      }
    }

    .sunCurve-loading {
      .ant-spin-dot {
        margin-top: 0 !important;
      }
    }
  }

  .pc-wrap {
    display: block;
  }

  .mobile-wrap {
    display: none;
  }
}

@include mobile-only {
  .sunCurve-wrap {
    .sunCurve-main {
      max-width: 100%;
    }
    .sunCurve-thead {
      display: none;
    }
    .sunCurve-main-inner {
      border-radius: 0px;
      background: transparent;
      overflow: visible;
    }
    .sunCurve-list {
      li {
        flex-wrap: wrap;
        padding: 10px;
        font-size: 12px;
        color: $color-text-default;
        margin-bottom: 12px;
        line-height: 17px !important;
        background: $color-table-bg;
        box-shadow: $color-curveindex-box-shadow;
        border-radius: 5px;
        border: 0;
        .sunCurve-value {
          font-size: 12px;
          font-weight: 600;
          color: $color-text-default;
        }
        .sunCurve-label {
          font-size: 12px;
          font-weight: 400;
          color: $color-text-default-5;
        }
      }
      .sunCurve-row-c,
      .sunCurve-row-r {
        .sunCurve-label {
          margin-right: 6px;
        }
      }
      .sunCurve-row-l {
        width: 100%;
        line-height: 20px;
        margin-right: 0;
        margin-bottom: 10px;
        .sunCurve-value {
          font-size: 12px;
          margin-right: 10px;
        }
      }
      .sunCurve-row-c,
      .sunCurve-row-r {
        .sunCurve-label {
          display: block;
        }
      }
      .sunCurve-row-c {
        flex: 1;
        margin-left: 0;
      }
      .sunCurve-row-r {
        flex: 1;
      }
      .sunCurve-row-l {
        .pool-icon {
          margin-right: 6px;
          height: 20px;
        }
      }
    }
    .more-btn {
      padding: 10px 0;
      line-height: 17px;
      font-size: 12px;
      border-radius: 5px;
      border: 0;
      background: $color-table-bg;
      box-shadow: $color-curveindex-box-shadow;
    }

    .sunCurve-deposite {
      margin: 0 auto;
      max-width: 100%;
      width: 100%;
      .sunCurve-deposite-header {
        display: block;
        margin: 30px auto 14px;
        font-size: 16px;
        line-height: 22px;
        .sunCurve-deposite-l {
          display: flex;
          align-items: center;
          line-height: 22px;
        }
        .sunCurve-deposite-total {
          margin-top: 10px;
          width: 100%;
          height: initial;
          text-align: left;
        }
        .sunCurve-deposite-total-label {
          margin-right: 6px;
          font-size: 14px;
        }
        .sunCurve-deposite-total-num {
          font-size: 20px;
        }
      }

      .sunCurve-deposite-list {
        .card-wrap {
          min-height: 80px;
        }
      }

      .sunCurve-loading {
        .ant-spin-dot {
          margin-top: -20px !important;
        }
      }
    }

    .pc-wrap {
      display: none;
    }

    .mobile-wrap {
      display: block;
    }
  }
}
.more-btn-icon {
  margin-left: 7px;
}
