@import '../../assets/css/var.scss';
@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.poolRow {
  display: flex;
  line-height: 20px;
  .poolRow-icon {
    margin-right: 6px;
    height: 20px;
    img {
      display: block;
      height: 100%;
    }
  }
  .poolRow-value {
    margin-right: 10px;
    line-height: 20px;
    font-size: 14px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: $color-text-default;
  }
  .poolRow-label {
    font-size: 12px;
    line-height: 20px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-7;
  }
}
