@import '../../assets/css/var.scss';
@import '../../assets/css/theme';
@import '../../assets/css/mixins';
.positionCard {
  background: $color-modal-bg;
  border-radius: 5px;

  .card-top {
    padding: 10px 10px 12px;
    .card-header {
      display: flex;
      margin-bottom: 10px;
      .card-header-l {
        flex: 1;
      }
      .card-header-r {
        line-height: 17px;
        font-size: 12px;
        font-weight: 600;
        color: $color-text-default;
        line-height: 17px;
      }
    }
    .card-body {
      .card-row {
        display: flex;
        margin-bottom: 8px;
        font-size: 12px;
        line-height: 17px;
        &:last-child {
          margin-bottom: 0;
        }
        .card-row-label {
          margin-right: 6px;
          color: $color-text-default-5;
        }
        .card-row-value {
          flex: 1;
          width: 0;
          color: $color-text-default;
        }
      }
    }
  }

  .card-btm {
    display: flex;
    border-top: 1px solid $color-text-default-05;
    button {
      position: relative;
      padding: 8px 0;
      height: auto;
      flex: 1;
      background: transparent;
      color: $color-button-link-text;
      &:hover,
      &:active {
        color: rgba(193, 160, 255, 0.7) !important;
      }
      &::after {
        position: absolute;
        right: 0;
        top: 0;
        content: ' ';
        width: 1px;
        height: 100%;
        background: $color-text-default-05;
      }
    }
  }
}
@include mobile-only {
  .positionCard {
    .card-btm {
      button {
        &:hover,
        &:active {
          background: transparent !important;
        }
      }
    }
  }
}
